import { isNgTemplate } from '@angular/compiler';
import { Component, OnInit, Input, IterableDiffers } from '@angular/core';
import { EventsService } from '../../services/events.service';
import { IEventData } from '../../interfaces/event';
import { Router } from '@angular/router';

@Component({
  selector: 'sb-event-detail',
  templateUrl: './event-detail.component.html',
  styleUrls: ['./event-detail.component.css']
})
export class EventDetailComponent implements OnInit {
  @Input() configData: any;
  identifier: number;
   item: any;
  constructor(private eventsService: EventsService, private _router: Router) {

  }

  ngOnInit() {
    this.identifier = this.configData['identifier'];
    console.log(this.configData['identifier']);

    this.eventsService.getEvent().subscribe((data: IEventData.IEvent) => {
        this.item = data.data.result[0];
        console.log();
      },
      (err: any) => {
        console.log('err = ', err);
      });
  }

  joinEvent()
  {
    //alert('hi');
    this._router.navigate(['/events']);
  }
}





